.background_navbar {
  background-color: #131a22;
}

.navbar_container {
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  height: 80px;
  font-weight: 700;
  width: 90%;
  margin: auto;
}

.navbar_leftSide {
  flex: 20%;
  max-width: 20%;
  display: flex;
  justify-content: center;
  min-width: 150px;
}

.search_bar {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  flex: 30%;
  max-width: 30%;
  min-width: 300px;
}

.navbar_rightSide {
  display: flex;
  flex: 20%;
  max-width: 20%;
  justify-content: space-evenly;
}

.navbar_rightSide > a,
.navbar_rightSide > div > div > p {
  padding: 1rem;
  min-width: 50px;
}

/* .navbar_rightSide:last-child {
  margin-right: 5px;
} */

.myshop_logo {
  text-align: center;
  flex-grow: 1;
}

.text-link {
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  text-decoration: inherit;

  border-radius: 5px;
  border: 2px solid #131a22;
}

@media (min-width: 600px) {
  .text-link {
  }
}

.text-link:hover {
  color: grey;
  border: 2px solid grey;
}

/* buttons Style */
.button_search {
  padding: 7px;
  margin-left: 2px;
  border: none;
  background: white;
  font-size: 20px;
  cursor: pointer;
}
.button_search:active {
  background: grey;
  border: none;
}

.button_search {
  border-radius: 5px;
  background: linear-gradient(to right, #191654, #43c6ac);
  background: -webkit-linear-gradient(to right, #191654, #43c6ac);
  color: white;
  font-size: 15px;
  background-size: 200%;
  background-position: left;
}

.button_search:hover {
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
  background-image: linear-gradient(to right, #191654, #43c6ac);
  background-position: right;
  animation: bg-animation 1.5s;
}
